input[type="submit"],
input[type="reset"],
input[type="button"],
input[type=date],
button,
select,
input[type=text] {
  /*去掉苹果的默认UI来渲染按钮 、解决部分手机浏览器对border：none无效办法*/
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  /*去掉微信端input-text在ios轻快下的点击出先黑色半透明背景*/
  tap-highlight-color: transparent !important;
}
a,
input[type=text],
select,
input[type=password],
textarea {
  /*去掉微信端input-text在ios轻快下的点击出先黑色半透明背景*/
